import React, { useState, useEffect } from 'react';
import { useModal } from '../ModalContext';
import { theme } from '../../theme.js';

const ContactUs = () => {
  const { showModal, toggleModal } = useModal();

  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    handleResize();
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const email = 'contact@stacqtrace.com';

  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    company: '',
    message: '',
    access_key: '33f6cc63-ec36-4146-b348-65aa2db8ee6a'
  });

  const containerStyle = {
    maxWidth: '600px',
    margin: '0 auto',
    padding: '20px'
  };

  const titleStyle = {
    textAlign: 'center',
    marginBottom: '20px',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    fontSize: '2rem',
    fontWeight: 'bold',
  };

  const subtitleStyle = {
    textAlign: 'center',
    marginBottom: '20px',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    fontSize: '1rem',
    fontWeight: '200'
  };

  const formStyle = {
    display: 'flex',
    flexDirection: 'column'
  };

  const formGroupStyle = {
    marginBottom: '15px'
  };

  const labelStyle = {
    fontWeight: 'bold',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary
  };

  const inputStyle = {
    width: '100%', // Ensure all input fields occupy full width
    padding: '8px',
    fontSize: '1rem',
    border: '1px solid #ccc',
    borderRadius: '4px',
    boxSizing: 'border-box', // Ensure padding and border are included in width calculation
    marginTop: '5px'
  };

  const textAreaStyle = {
    width: '100%', // Ensure text area occupies full width
    padding: '8px',
    fontSize: '1rem',
    border: '1px solid #ccc',
    borderRadius: '4px',
    boxSizing: 'border-box', // Ensure padding and border are included in width calculation
    resize: 'vertical', // Allow vertical resizing of text area
    marginTop: '5px'
  };

  const submitButtonStyle = {
    width: '100%', // Ensure submit button occupies full width
    backgroundColor: theme.colors.primary,
    color: '#fff',
    border: 'none',
    borderRadius: '5px',
    cursor: 'pointer',
    fontFamily: theme.fonts.primary,
    marginTop: '10px', // Added margin for spacing
    height: '50px',
    fontSize: '1rem'
  };

  const overlayStyle = {
    position: 'fixed',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    display: showModal ? 'block' : 'none',
    zIndex: 1000
  };

  const popupStyle = {
    position: 'fixed',
    borderRadius:10,
    top: '50%',
    left: '50%',
    width: isMobile ? '80%' : '50%',
    transform: 'translate(-50%, -50%)',
    backgroundColor: '#fff',
    padding: '20px',
    boxShadow: '0 0 10px rgba(0, 0, 0, 0.1)',
    zIndex: 1001,
    display: showModal ? 'block' : 'none',
    maxWidth: '80%',
    maxHeight: '80%',
    overflow: 'auto'
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    const formDataToSend = new FormData();
    formDataToSend.append('name', formData.name);
    formDataToSend.append('email', formData.email);
    formDataToSend.append('message', formData.message);
    formDataToSend.append('access_key', formData.access_key);

    try {
      const response = await fetch('https://api.web3forms.com/submit', {
        method: 'POST',
        body: formDataToSend
      });

      if (response.ok) {
        alert('Form submitted successfully!');
        setFormData({
          name: '',
          email: '',
          phone: '',
          company: '',
          message: ''
        });
        toggleModal(); // Close modal after successful submission
      } else {
        alert('Failed to submit form. Please try again later.');
      }
    } catch (error) {
      console.error('Error submitting form:', error);
      alert('An error occurred while submitting the form. Please try again later.');
    }
  };

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData({ ...formData, [name]: value });
  };

  return (
    <div style={containerStyle}>
      
      {/* Modal/Popup */}
      <div style={overlayStyle} onClick={toggleModal}></div>
      <div style={popupStyle}>
        <p style={titleStyle}>Contact Us</p>
        <div style={subtitleStyle}>
          <span>For inquiries regarding quotes or further information, you can also email us at </span>
          <a href={`mailto:${email}`} style={{ color: theme.colors.primary, cursor: 'pointer', textDecoration: 'underline' }}>
            {email}
          </a>.
        </div>
        <form style={formStyle} onSubmit={handleSubmit}>
          <div style={formGroupStyle}>
            <label style={labelStyle}>Name</label>
            <input
              type="text"
              name="name"
              value={formData.name}
              onChange={handleChange}
              style={inputStyle}
              required
            />
          </div>
          <div style={formGroupStyle}>
            <label style={labelStyle}>Email</label>
            <input
              type="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              style={inputStyle}
              required
            />
          </div>
          <div style={formGroupStyle}>
            <label style={labelStyle}>Message</label>
            <textarea
              name="message"
              value={formData.message}
              onChange={handleChange}
              style={textAreaStyle}
              rows={4}
              required
            />
          </div>
        <button type="submit" style={submitButtonStyle}>Submit</button>
      </form>
      </div>
    </div>
  );
};

export default ContactUs;
